.articles-container {
	max-width: 1180px;
	margin: 0 auto;
	&__header {
		position: relative;
		&:before {
			@include pseudo;
			bottom: 0;
			border-bottom: 1px solid $really-grey;
			top: auto;
			opacity: 0.5;
			width: 100%; }
		.more-link {
			position: absolute;
			bottom: 15px;
			right: 0; } }
	&__title {
		display: inline-block;
		font-size: 30px;
		padding-bottom: 10px;
		position: relative;
		@include PSemi;
		&:before {
			@include pseudo;
			top: auto;
			bottom: -0.5px;
			background-color: $cyan;
			height: 3px;
			width: 100%; } }
	&__body {
		display: flex;
		flex-direction: row; }
	.item {
		&__title {
			font-size: 14px; } }
	.popular-articles {
		padding: 5px 0;
		min-width: 280px; } }
