.button {
	color: #fff;
	font-size: 15px;
	border-radius: 30px;
	padding: 12px 21px 11px;
	@include PSemi;
	display: inline-block;
	@include trans;
	&_cyan {
		background-color: $cyan; }
	&_hero {
		padding: 12px 32px 8px;
		border: 1px solid $cyan;
		color: $cyan;
		letter-spacing: 0.5px;
		@include PBold; }
	&_border-white {
		background-color: transparent;
		border: 1px solid #fff;
		color: #fff;
		padding: 11px 21px 10px;
		&:hover {
			background-color: #fff;
			color: $cyan; } }
	&_hero {
		padding: 18px 73px;
		@include PBold;
		font-size: 20px; } }

.btn {
	padding: 10px 15px 11px;
	border-radius: 20px;
	color: $white;
	font-size: 16px;
	border: none;
	display: inline-block;
	vertical-align: top;
	appearance: none;
	@include PBold;
	text-align: center;
	&--w-240 {
		min-width: 240px; }
	&--pink {
		background-color: $pink; } }
.btnNext, .btnPrev {
	width: 28px;
	height: 28px;
	background-color: $little-grey;
	cursor: pointer;
	text-align: center;
	border-radius: 50%;
	position: absolute;
	i {
		@include vcenter; }
	svg {
		width: 7px;
		height: 12px;
		fill: #14c1e5; }
	&.slick-disabled {
		cursor: default;
		@include nosel;
		user-select: none; } }

.more-link {
	color: $cyan;
	@include OSLight;
	position: relative;
	&:before {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		content: '';
		background-image: url("../img/arrow-sm.png");
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center; }
	span {
		margin-left: 20px; }
	&--sm {
		font-size: 14px;
		&:before {
			top: 2px;
			height: 16px;
			width: 16px; } } }

