.services {
	background-color: $white;
	padding: 100px 20px;
	&__title {
		font-size: 35px;
		margin-bottom: 58px;
		text-align: center;
		@include PSemi; }
	&__in {
		max-width: 1180px;
		margin: 0 auto; }
	&__row {
		display: flex;
		justify-content: space-between;
		margin-bottom: 50px;
		&:last-of-type {
			margin-bottom: 0; } }
	&__item-title {
		font-size: 20px;
		@include PBold; }
	&__item {
		width: 333px; }
	&__list {
		margin-top: 10px; }
	&__list-item {
		display: block;
		position: relative;
		&:before {
			@include pseudo;
			top: auto;
			bottom: 5px;
			border-bottom: 1px dotted #b8b8b8;
			width: 100%; } }
	&__name {
		display: inline-block;
		font-size: 17px;
		position: relative;
		padding-right: 3px;
		@include OSReg; }
	&__name,
	&__value {
		background-color: $white;
		z-index: 5; }
	&__value {
		bottom: 0;
		color: $bright-blue;
		font-size: 15px;
		font-weight: 600;
		padding-left: 3px;
		position: absolute;
		right: 0;
		@include OSSemi; }
	&__link {
		margin-top: 12px; } }

