.meta-filters {
	font-size: 0;
	margin-bottom: 20px;
	border-radius: 5px;
	box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
	overflow: hidden;
	display: flex;
	.meta-filter {
		border-right: 1px solid rgba($really-grey, 0.5);
		&:last-of-type {
			border-right: none; } }
	&--mb-31 {
		margin-bottom: 31px; }
	&--w-830 {
		max-width: 830px; } }
.meta-filter {
	// display: inline-block
	// vertical-align: top
	// width: 25%
	flex-basis: 25%;
	min-height: 45px;
	background-color: $white;
	text-align: center;
	padding: 17px 15px 14px;
	line-height: 1;
	@include trans;
	&:hover {
		background-color: $cyan;
		.meta-tag, span {
			color: $white; } }
	.meta-tag {
		text-transform: none;
		display: inline-block;
		vertical-align: top;
		&__circle {
			top: 1px; } }
	.meta-tag, span {
		color: $black;
		@include PReg;
		font-size: 14px;
		@include trans; } }
